import React from "react";
import { Spring } from "react-spring/renderprops";
import "./Festivali19.scss";
import Bf18 from './img/bf.jpeg';
import Dani from './img/dani.jpg';
import './Festivali19.scss';


export default function Festivali19 () {
  return (
    
    <Spring
      from={{ opacity: 0, marginTop: -500 }}
      to={{ opacity: 1, marginTop: 0 }}
    >
      {props => (
        <div style={props}>
        <div>
        <div className='naslov'>
        <h1>FESTIVALI U 2019</h1>
        </div>
                     
            <br />  
            <br />          
            <div className='lista'>
              
                <div className='prviFestival'>       
                   <h2>Beogradski festival piva</h2>
                   <img className='imgfest1' src={Bf18}  alt="slika3" />
                    <p className='paragraf1'>                    
                    Organizatori festivala posebnu zahvalnost duguju slubama grada Beograda bez ije 
                    podrke ne bi bilo mogue realizovati festival ovog obima. 
                    Svakog dana oko 100 radnika sa 15 vozila JKP "Gradska istoa" i "Zelenilo Beograd" uklanjalo smea, 
                    a separacija otpada se radila na licu mesta, tako da je prikupljeno vie od 3 i po tone limenki za reciklau. 
                    Na signalizaciji oko festivalskog prostora je bilo angaovano 20 radnika i 6 vozila, 
                    a sve vreme trajanja festivala su deurale i ekipe elektriara i javnog osvetljenja.
                    </p>
                </div>
              
                <div className='drugiFestival'>                    
                    <h2>Dani piva u Zrenjaninu</h2>
                    <img className='imgfest2' src={Dani}  alt="slika4" />
                    <p className='paragraf2'>                    
                    Dani piva traju do nedelje, 2. septembra, uz raznovrstan program na vie lokacija. 
                     To je jedna od najveih tradicionalnih manifestacija u Srbiji, a svakako prepoznatljiv 
                     zrenjaninski turistiki brend. Izmeu ostalih, nastupie: Bajaga i instruktori, Riblja orba, 
                        Darko Rundek, Haris Dinovi, Marija erifovi, Deca loih muziara, Goblini, Neverne bebe, 
                        Garavi Sokak, elko Vasi i drugi.Ovo jeste najznaajnija turistika manifestacija u naem gradu. 
                        Oekujemo, pre svega, lepo vreme i da sve proe u najboljem redu. Napravili smo dobar program. 
                        Preko 2.500 hiljade uesnika u programima za ovih sedam dana. Uvek nam je elja da budemo meu 
                        najboljima u Srbiji i sigurno je ova naa manifestacija jedna od najboljih, dodao je Janji.
                        Nakon sveanog otvaranja manifestacije na itnom trgu, u utorak, 28. avgusta, u 21 sat, nastupie Garavi sokak, 
                        a dva asa kasnije eljko Vasi. Svih narednih veeri, na centralnoj lokaciji odravae se po tri koncerta, 
                        u terminima od 19:30, 21:00 i 23:00, saoptili su organizatori.   
                        </p>
                </div>
            </div>
          
        </div>
        </div>
      )}
    </Spring>
  );
}
