import React from "react";
import { Spring } from "react-spring/renderprops";
import IPA from '../img/IPA.jpeg';
import APA from '../img/APA.jpeg';
import "./Ale.scss";

export default function Ale() {
  return (
    
    <Spring
      from={{ opacity: 0, marginTop: -500 }}
      to={{ opacity: 1, marginTop: 0 }}
    >
      {props => (
        <div style={props}>
          <div className='alestyle'>
          
            <h1>Ale</h1>
            
            <img className='IPAimg' src={IPA}  alt="slika3" />
            <p className='parag1'>
            IPA (India Pale Ale) je zapravo stil piva pale ale koji se prvobitno razvio u Engleskoj. 
            U vreme dok je Indija bila britanska kolonija, velike koliine ovog piva su brodovima 
            dopremane britanskim vojnicima na slubi u Indiji. Kako bi sauvali kvalitet i arome 
            piva tokom dugog puta, dosetili su se da dodaju velike koliine hmelja, sluio je kao konzervans, 
            pa je u ovim ejlovima bilo i vie alkohola. Otuda u imenu ovog pale ale-a India. 
            Danas je IPA jedno od najeih i jedinstvenih stilova amerikih kraft piva.             
            </p>
          <br />
          <br />
          <img className='APAimg' src={APA}  alt="slika3" />
            <p className='parag2'>
            APA (American Pale Ale) je najei stil piva u Americi. 
            Istorija nastanka APA-e nas opet vraa na Engleski Pale Ale. 
            Kao i za mnoga amerika piva, engleska istorija pivarstva je i ovde ostavila trag. 
            Ameriko Pale Ale pivo je nastalo poetkom 80-tih kada su manje pivare poele 
            da koriste amerike sorte hmelja (Cascade ili Amarillo). 
            U odnosu na britanska Pale Ale piva Ameriiki Pale karakterie jai ukus hmelja i aroma citrusa.  
            
            </p>
            
          </div>
        </div>
      )}
    </Spring>
  );
}
